/* Imports ----------------------------------------------------------------- */

// D3 imports commented out for development: UNCOMMENT for production
// import "whatwg-fetch";
import * as d3 from "d3";

/* Dataset ----------------------------------------------------------------- */

const n = 100;
const dataset = d3.range(n).map((d, i) => {
    const intercept = 40;
    const random = d3.randomNormal(0, 10)();
    const value = intercept + i + random;
    return value;
});

/* Styles ------------------------------------------------------------------ */

function addStyleSheet(href, onLoad) {

    // Prevent for adding the same stylesheet twice
    for(let i = 0; i < document.styleSheets.length; i++) {
        if(document.styleSheets[i].href == href) {
            return;
        }
    }

    const head = document.getElementsByTagName("head")[0];
    const link = document.createElement("link");
    link.rel  = "stylesheet";
    link.type = "text/css";
    link.href = href;
    if (onLoad) { link.onload = () => onLoad() }
    head.appendChild(link);
}

/* Draw chart -------------------------------------------------------------- */

function drawGraphic(containerWidth) {

    const margin = {top: 50, right: 50, bottom: 50, left: 50};
    const width = containerWidth - margin.left - margin.right;
    const height = containerWidth * 0.7 - margin.top - margin.bottom;

    const xScale = d3.scaleLinear()
        .domain([0, n])
        .range([0, width]);

    const yScale = d3.scaleLinear()
        .domain([0, n * 1.6])
        .range([height, 0]);

    const line = d3.line()
        .x((d, i) => xScale(i + 1))
        .y(d => yScale(d));

    d3.select("#vis").selectAll("*").remove();

    const vis = d3.select("#vis")
        .append("svg")
        .attr("id", "graphic")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    const xAxis = vis.append("g")
        .attr("class", "axis xaxis")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(xScale));

    const yAxis = vis.append("g")
        .attr("class", "axis yaxis")
        .call(d3.axisLeft(yScale));

    const path = vis.append("path")
        .datum(dataset)
        .attr("class", "line")
        .attr("d", line);
}

/* Draw -------------------------------------------------------------------- */

function drawGraphicInContext() {
    const rect = document.getElementById("vis").getBoundingClientRect();
    drawGraphic(rect.width);
}

/* Main -------------------------------------------------------------------- */

window.addEventListener('resize', () => drawGraphicInContext());
addStyleSheet("https://visual.parliament.uk/research/visualisations/responsive-chart-test/style.css", () => drawGraphicInContext());
